<template>
  <div id="login">
    <headpage></headpage>
    <div>
      <!-- 登录表单 -->
      <center>
        <div style="margin:0.8rem 0 0.8rem 0">
            <span>
              <strong>用户登录</strong>
            </span>
          </div>
        <form class="form-horizontal col-md-offset-1 col-sm-10" >
          <div class="form-group">
            <label  class="col-sm-5 col-xs-4 control-label" >用户名：</label> 
            <div id="v" class="col-sm-2 col-xs-6" style="padding:0px;">
              <input
                id="u"
                type="text"
                v-model="loginForm.username"
                class="form-control"
                placeholder="请输入用户名或手机号"
              />
            </div>
          </div>
          
          <div class="form-group">
            <label class="col-sm-5 col-xs-4 control-label">&nbsp;&nbsp;&nbsp;&nbsp;密码：</label>
            <div class="col-sm-2 col-xs-6" style="padding:0px;">
              <input
                id="p"
                type="password"
                v-model="loginForm.password"
                class="form-control"
                placeholder="请输入密码"
              />
            </div>
          </div>
          <div class="form-group">
            <div class="col-sm-offset-5 col-sm-2 col-xs-offset-1">
              <input
                type="button"
                @click.prevent="login"
                data-toggle="modal"
                data-target="#window"
                value="用户登录"
                class="col-sm-offset-1 col-sm-10 col-xs-offset-2 col-xs-6 btn btn-primary"
              />
            </div>
          </div>
          <div class="form-group">
            <div class="col-sm-offset-5 col-sm-2 col-xs-offset-1">
              <input
                type="button"
                @click="toRegister"
                value="用户注册"
                class="col-sm-offset-1 col-sm-10 col-xs-offset-2 col-xs-6 btn btn-primary"
              />
            </div>
          </div>
          <div class="form-group">
            <div class="col-sm-offset-5 col-sm-2 col-xs-offset-1">
              <input
                type="button"
                @click="toResetPwd"
                value="忘记密码"
                class="col-sm-offset-1 col-sm-10 col-xs-offset-2 col-xs-6 btn btn-primary"
              />
            </div>
          </div>
        </form>
      </center>
    </div>
    <footpage class="footpage"></footpage>
    <!-- 提示框 -->
    <div
      class="modal fade"
      id="window"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">贝叶科技提示:</h4>
          </div>
          <div class="modal-body">{{msg}}</div>
          <div class="modal-footer">
            <button type="button" id="ok" @click="ok" class="btn btn-primary">确认</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import axios from "axios";
import qs from "qs";

import headpage from "@/components/headbarLogin";
import footpage from "@/components/footbar";

export default {
  name: "login",
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      },
      msg: ""
    };
  },
  components: {
    headpage,
    footpage
  },
  methods: {
    // 登录事件
    login() {
      let _this = this;
      let params = {
        name: this.loginForm.username,
        passwd: this.loginForm.password
      };
      if (this.loginForm.username == "" || this.loginForm.password == "") {
        this.msg = "账号或密码不能为空";
      } else {
        axios
          .post("http://152.136.211.215:8080/user/login", qs.stringify(params))
          .then(response => {
            if (response.status === 200 && response.data.code === 0) {
              this.msg = "登录成功！";
              //存储用户名
              sessionStorage.setItem("userName", this.loginForm.username);
              this.$store.dispatch("userName", this.loginForm.username);

              return true;
            } else {
              this.msg = "账号或密码错误，请重新输入";
              return false;
            }
          });
      }
    },
    ok() {
      if (
        this.msg === "账号或密码不能为空" ||
        this.msg === "账号或密码错误，请重新输入"
      ) {
        $("#window").modal("toggle");
      } else {
        $("#window").modal("toggle");
        this.$router.push({ path: "./index" });
      }
    },
    // 跳转到注册页面
    toRegister() {
      this.$router.push({ path: "./register" });
    },
    // 跳转到找回密码页面
    toResetPwd() {
      this.$router.push({ path: "./resetPwd" });
    }
  }
};
</script>

<style lang="less" scoped>
// 背景图片
#login {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
li {
  list-style: none;
}
#u,#p{
  border: none ;
  box-shadow: none !important;
  background: none;
  border-radius: 0;
  border-bottom: 1px solid #337AB7
}
@media (min-width: 300px) {
  label{
    margin-top: 7px;
    margin-left: 18px;
    max-width: 24%;
  }
  .footpage{
    margin-top: 50%;
  }
}
@media (min-width: 900px) {
  label{
    margin-top: 0;
    margin-left: 0;
    max-width: 100%;
  }
  .footpage{
    margin-top: 5%;
  }
}
</style>



