<template>
  <div class="index">
    <headpage></headpage>
    <!-- 主页内容 -->
    <center>
      <div class="msg">
        <button id="upload" @click="getFile">
          点击
          <span class="glyphicon glyphicon-new-window"></span>
          上传皮肤病理图片
        </button>
        <input
          type="file"
          accept=".jpg, .png, .gif, .bmp, .jpeg"
          style="display:none"
          id="getF"
          @change="clickF"
        />
      </div>
    </center>
    <footpage class="footpage"></footpage>
  </div>
</template>

<script>
import headpage from "@/components/headbar";
import footpage from "@/components/footbar";
export default {
  data() {
    return {};
  },
  components: {
    headpage,
    footpage
  },
  methods: {
     //选择文件
    getFile() {
      $("#getF").click();
    },
     clickF(event) {
     }
  }
};
</script>

<style lang="less" scoped>
.index {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
#upload {
  border: none;
  background: none;
}
@media (min-width: 300px) {
.msg {
  margin-top: 70%;
  font-size: 15px;
  color: #191970;
  span {
    font-size: 20px;
  }
  
}
.footpage{
    margin-top: 55%;
  }
}
@media (min-width: 900px) {
  .msg {
  margin-top: 15%;
  font-size: 30px;
  color: #191970;
  span {
    font-size: 40px;
  }
}
.footpage{
    margin-top: 18%;
  }
}
</style>


