<template>
  <div id="resetPwd">
    <headpage></headpage>
    <center>
      <div>
        <center>
          <div style="margin:0.8rem 0 0.8rem 0">
            <span>
              <strong>重置密码</strong>
            </span>
          </div>
          <!-- 重置密码表单 -->
          <form class="form-horizontal col-md-offset-1 col-sm-10" role="form">
            <div class="form-group">
              <label class="col-sm-5 col-xs-4 control-label">&nbsp;&nbsp;&nbsp;&nbsp;手机号：</label>
              <div class="col-sm-2 col-xs-6" style="padding:0px;">
                <input type="text" id="p" v-model="phone" class="form-control" placeholder="请输入手机号" />
              </div>
            </div>
            <div class="form-group">
              <label class="col-sm-5 col-xs-4 control-label">设置密码：</label>
              <div class="col-sm-2 col-xs-6" style="padding:0px;">
                <input
                  type="password"
                  id="pw"
                  v-model="pwd"
                  class="form-control"
                  placeholder="请输入六至八位密码"
                />
              </div>
            </div>
            <div class="form-group">
              <label class="col-sm-5 col-xs-4 control-label">确认密码：</label>
              <div class="col-sm-2 col-xs-6" style="padding:0px;">
                <input
                  type="password"
                  id="pw1"
                  v-model="pwd1"
                  class="form-control"
                  placeholder="请再次输入您设置的密码"
                />
              </div>
            </div>
            <div class="form-group" id="verification">
              <label class="col-sm-5 col-xs-4 control-label">&nbsp;&nbsp;&nbsp;&nbsp;验证码：</label>
              <div class="col-sm-2 col-xs-6" style="padding:0px;">
                <input
                  type="text"
                  id="v"
                  v-model="VerificationCode"
                  class="form-control"
                  placeholder="请输入短信验证码"
                />
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-5 col-sm-2">
                <input
                  type="button"
                  id="getVerificationCode"
                  @click="getVerificationCode"
                  data-toggle="modal"
                  data-target="#window"
                  value="获取短信验证码"
                  class="col-sm-offset-1 col-sm-10 col-xs-offset-3 col-xs-6 btn btn-primary"
                />
              </div>
            </div>
            <div class="form-group">
              <div class="col-sm-offset-5 col-sm-2">
                <input
                  type="button"
                  @click="resetPwd"
                  id="btnResetPwd"
                  disabled="true"
                  data-toggle="modal"
                  data-target="#window"
                  value="重置密码"
                  class="col-sm-offset-1 col-sm-10 col-xs-offset-3 col-xs-6 btn btn-primary"
                />
              </div>
            </div>
          </form>
        </center>
      </div>
    </center>
    <footpage class="footpage"></footpage>
    <!-- 提示框 -->
    <div
      class="modal fade"
      id="window"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">贝叶科技提示:</h4>
          </div>
          <div class="modal-body">{{msg}}</div>
          <div class="modal-footer">
            <button type="button" id="ok" @click="ok" class="btn btn-primary">确认</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headpage from "@/components/headbarLogin";
import footpage from "@/components/footbar";
import axios from "axios";
import qs from "qs";

export default {
  data() {
    return {
      phone: "",
      pwd: "",
      pwd1: "",
      VerificationCode: "",
      msg: ""
    };
  },
  components: {
    headpage,
    footpage
  },
  methods: {
    // 获取验证码
    getVerificationCode() {
      // 信息验证
      if (this.phone === "") {
        this.msg = "手机号不能为空";
      } else if (String(this.phone).length !== 11) {
        this.msg = "请输入正确的手机号";
      } else if (this.pwd === "") {
        this.msg = "密码不能为空";
      } else if (String(this.pwd).length < 6 || String(this.pwd).length > 8) {
        this.msg = "请设置密码长度为6至8位";
      } else if (this.pwd1 !== this.pwd) {
        this.msg = "两次密码不一样，请重新输入";
      } else {
        //  获取短信验证码按钮信息改变
        let time = 60;
        $("#getVerificationCode").attr("disabled", true);
        let timer = setInterval(function() {
          if (time == 0) {
            clearInterval(timer);
            $("#getVerificationCode").attr("disabled", false);
            $("#getVerificationCode").val("获取短信验证码");
          } else {
            $("#getVerificationCode").val(time + "秒");
            time--;
          }
        }, 1000);
        // 将手机号发送给后台
        let params = {
          phoneNumber: this.phone
        };
        axios
          .post(
            "http://152.136.211.215:8080/user/getVerificationToResetPwd",
            qs.stringify(params)
          )
          .then(response => {
            console.log(response);
            if (response.data.code === 0) {
              // 显示输入验证码
              $("#verification").toggle();
              $("#btnResetPwd").attr("disabled", false);
              this.msg = "发送成功，请注意查收验证码";
              $("#window").modal("toggle");
            } else {
              this.msg = "发送失败，请检查您的手机号是否正确后重新获取验证码";
              $("#window").modal("toggle");
              clearInterval(timer);
              $("#getVerificationCode").attr("disabled", false);
              $("#getVerificationCode").val("获取短信验证码");
            }
          });
      }
    },
    //重置密码
    resetPwd() {
      let params = {
        name: this.phone,
        pwd: this.pwd,
        code: this.VerificationCode
      };
      axios
        .post("http://152.136.211.215:8080/user/resetPwd", qs.stringify(params))
        .then(response => {
          console.log(response);
          if (response.data.code === 0) {
            this.msg = "重置密码成功,请登录";
          } else if (response.data.code === -1) {
            this.msg = "验证码错误，请重新输入";
          } else {
            this.msg = "重置密码失败";
          }
        });
    },
    ok() {
      $("#window").modal("toggle");
      if (this.msg === "重置密码成功,请登录") {
        this.$router.push({ path: "./" });
      } else {
      }
    }
  }
};
</script>

<style lang="less" scoped>
#resetPwd {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
#verification {
  display: none;
}
#p,
#pw,
#pw1,
#v {
  border: none;
  box-shadow: none !important;
  background: none;
  border-radius: 0;
  border-bottom: 1px solid #337ab7;
}
@media (min-width: 300px) {
  label{
    margin-top: 7px;
    margin-left: 18px;
    max-width: 28%;
  }
  .footpage{
    margin-top: 50%;
  }
}
@media (min-width: 900px) {
  label{
    margin-top: 0;
    margin-left: 0;
    max-width: 100%;
  }
  .footpage{
    margin-top: 5%;
  }
}
</style>



