<template>
  <div>
    <img class="logo" src="../img/logo.png" alt="logo" />
    <span class="name">{{name}}</span>
    <div class="ing" id="ing">
      <img src="../img/i.gif" />
      <span style="text-algin:center">上传中...</span>
    </div>
    <!-- 上传图片 -->
    <button id="upload" @click="getFile">
      <span class="glyphicon glyphicon-new-window"></span>
    </button>
    <!-- 规定上传图片的格式 -->
    <input
      type="file"
      accept=".jpg, .png, .gif, .bmp, .jpeg"
      style="display:none"
      id="getF"
      @change="clickF"
    />
    <!-- 刷新 -->
    <router-link tag="span" to="index">
      <span id="refresh" class="glyphicon glyphicon-repeat"></span>
    </router-link>
    <!-- 个人中心 -->
    <router-link tag="span" to="user">
      <span id="user" class="glyphicon glyphicon-user"></span>
    </router-link>
    <!-- 上传成功提示框 -->
    <div
      class="modal fade"
      id="window"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">贝叶科技皮肤病理 AI 分析系统提示:</h4>
          </div>
          <div class="modal-body">{{msg}}</div>
          <div class="modal-footer">
            <button type="button" id="ok" @click="ok" class="btn btn-primary">确认</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapMutations } from "vuex";
import qs from "qs";

export default {
  data() {
    return {
      name: "Dermatopathology Analysis 皮肤病理 AI 分析",
      username: sessionStorage.getItem("userName"),
      $filePath: "",
      filename: "",
      msg: ""
    };
  },
  methods: {
    // 判断是否登录,如果没有登录，则提示登录，并跳回登录页面
    isLogin() {
      if (sessionStorage.getItem("userName") !== "null") {
        this.$store.commit("userStatus", sessionStorage.getItem("userName"));
      } else {
        this.$store.commit("userStatus", null);
        this.msg = "请先登录！";
        $("#window").modal("toggle");
      }
      return this.$store.getters.isLogin;
    },
    // 获取当前年月日，格式为190726
    getDate() {
      let d = new Date();
      let year = d.getFullYear();
      year = year.toString().slice(2, 4);
      let month = d.getMonth() + 1;
      let day = d.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (day >= 0 && day <= 9) {
        day = "0" + day;
      }
      let dt = year + month.toString() + day.toString();
      return dt;
    },
    //获取当前小时分钟秒数,格式为171355
    getTime() {
      let d = new Date();
      let hour = d.getHours();
      let minute = d.getMinutes();
      let second = d.getSeconds();
      if (hour >= 1 && hour <= 9) {
        hour = "0" + hour;
      }
      if (minute >= 1 && minute <= 9) {
        minute = "0" + minute;
      }
      if (second >= 1 && second <= 9) {
        second = "0" + second;
      }
      let dt = hour.toString() + minute.toString() + second.toString();
      return dt;
    },
    //上传图片
    getFile() {
      $("#getF").click();
    },
    clickF(event) {
      $("#ing").toggle();
      this.$filePath = URL.createObjectURL(event.target.files[0]); //将上传的图片显示在下一页面，解析后的绝对路径
      let file = event.target.files[0]; //获取文件名和文件信息，传给后台

      this.filename =
        this.username +
        "-" +
        this.$options.methods.getDate() +
        "-" +
        this.$options.methods.getTime();
      //console.log(this.filename);
      let param = new FormData(); //创建form对象
      param.append("qqfile", file); //通过append向form对象添加数据
      param.append("fileName", this.filename);
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      }; //添加请求头
      axios
        .post("http://123.206.92.104:8009/upload", param, config)
        .then(response => {
          if (response.data.success === true) {
            $("#ing").toggle();
            this.msg = "上传成功";
            $("#window").modal("toggle");
          } else {
            this.msg = "上传失败";
            $("#window").modal("toggle");
          }
        });
    },
    ok() {
      if (this.msg === "请先登录！") {
        this.$router.push({ path: "./" });
      }
      if (this.msg === "上传成功") {
        $("#window").modal("toggle");
        this.$router.push({
          path: "./prediction",
          //将图片路径以及图片名传给预测页面
          query: {
            fileUrl: this.$filePath,
            processed_file: this.filename
          }
        });
      } else {
        $("#window").modal("toggle");
      }
    }
  },
  mounted() {
    this.isLogin();
  }
};
</script>

<style lang="less" scoped>

@media (min-width: 300px) {
  .logo {
    width: 40%;
    margin-top: 5%;
    margin-left: 30%;
  }
  .ing {
    display: none;
    position: fixed;
    top: 8%;
    left: 25%;
    z-index: 3;
  }
  .name{
    display: block;
    margin-top: 1%;
    margin-left: 6%;
    color: #191970;
  }
  span {
    font-size: 16px;
    margin-top: 2%;
    color: #191970;
  }
  #upload {
    margin-top: 1%;
    margin-left: 60%;
    border: none;
    background: none;
  }
  #refresh,
  #user {
    margin-top: 1%;
    margin-left: 5%;
  }
}
@media (min-width: 992px) {
  .logo {
    width: 19.2%;
    margin-top: 1%;
    margin-left: 5%;
  }
  .ing {
    display: none;
    position: fixed;
    top: 10%;
    left: 40%;
    z-index: 3;
  }
  // 标题
  .name {
    display: inline;
    margin-top: 1%;
    margin-left: 1%;
    color: #191970;
  }
  span {
    display: inline;
    font-size: 25px;
    margin-top: 2%;
    color: #191970;
  }
  // 上传按钮
  #upload {
    margin-top: 1%;
    margin-left: 8%;
    border: none;
    background: none;
  }
  // 刷新、用户中心按钮
  #refresh,
  #user {
    margin-top: 1%;
    margin-left: 5%;
  }
}
</style>



