<template>
  <div class="prediction">
    <headpage></headpage>
    <div class="body">
      <!-- 左侧内容 -->
      <div class="left">
        <span class="left__msg">上传的病理图片</span>
        <br />
        <img class="left__img" id="illness" :src="this.url" alt="病理图片" />
      </div>
      <!-- 中间内容 -->
      <div class="center">
        <div class="center__process" id="process">
          <img src="../img/iii.gif" />
          <br />
          <br />
          <span>分析中...</span>
        </div>
        <div class="center__prediction" id="prediction">
          <span class="t">炎症模式</span>
          <div class="center__prediction-msg">
            <span>&nbsp;&nbsp;{{msg}}</span>
            <br />
            <span class="center__prediction-info">&nbsp;&nbsp;浅层血管周围皮炎</span>
          </div>
          <div style="text-align:center;margin-top:5%">
            <button @click="yes" class="btn btn-success">是</button>
            <button style="margin:0 0 0 25%" @click="no" class="btn btn-danger">否</button>
          </div>
        </div>
      </div>
      <!-- 右侧内容 -->
      <div class="right">
        <span>近似的病理图片</span>
        <div style="margin: 3% 0 0 0;">
          <img :src="this.imgSS.imgSrc0" />
          <img :src="this.imgSS.imgSrc1" />
        </div>
        <div style="margin-top:3px">
          <img :src="this.imgSS.imgSrc2" />
          <img :src="this.imgSS.imgSrc3" />
        </div>
      </div>
    </div>
    <footpage class="footpage"></footpage>
    <!-- 意见反馈 -->
    <div
      class="modal fade"
      id="judge_window"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">贝叶科技皮肤病理 AI 分析系统提示:</h4>
          </div>
          <div class="modal-body">
            <span>您的判断：</span>
            <input type="text" v-model="estimate" />
            <br />
            <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;原因：</span>
            <br />
            <textarea cols="25" rows="4" v-model="judge" placeholder="200字以内"></textarea>
          </div>
          <div class="modal-footer">
            <button type="button" id="sub_judge" @click="sub_judge" class="btn btn-primary">提交</button>
          </div>
        </div>
      </div>
    </div>
    <!-- 提示框 -->
    <div
      class="modal fade"
      id="window"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">贝叶科技皮肤病理 AI 分析系统提示:</h4>
          </div>
          <div class="modal-body">{{alert}}</div>
          <div class="modal-footer">
            <button type="button" id="ok" @click="ok" class="btn btn-primary">确认</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import headpage from "@/components/headbarPrediction";
import footpage from "@/components/footbar";
import qs from "qs";

export default {
  data() {
    return {
      url: "",
      msg: "",
      alert: "",
      fileName: "",
      estimate: "",
      judge: "",
      //预测图片列表
      imgSS: {
        imgSrc0: require("../img/ii.gif"),
        imgSrc1: require("../img/ii.gif"),
        imgSrc2: require("../img/ii.gif"),
        imgSrc3: require("../img/ii.gif")
      },
      imgList: [
        {
          imgSrc0: require("../img/0/8357C嗜酸细胞增多4倍.jpg"),
          imgSrc1: require("../img/0/8446B湿疹4倍.jpg"),
          imgSrc2: require("../img/0/8480D海绵4倍.jpg"),
          imgSrc3: require("../img/0/8512C海绵10倍.jpg")
        },
        {
          imgSrc0: require("../img/1/9494C.jpg"),
          imgSrc1: require("../img/1/9494D.jpg"),
          imgSrc2: require("../img/1/9495A.jpg"),
          imgSrc3: require("../img/1/9562E.jpg")
        },
        {
          imgSrc0: require("../img/2/8983D.jpg"),
          imgSrc1: require("../img/2/9286B.jpg"),
          imgSrc2: require("../img/2/9373B.jpg"),
          imgSrc3: require("../img/2/9208C.jpg")
        },
        {
          imgSrc0: require("../img/3/8956B硬萎4倍.jpg"),
          imgSrc1: require("../img/3/9016D扁平苔藓4倍.jpg"),
          imgSrc2: require("../img/3/9151B扁平苔藓4倍.jpg"),
          imgSrc3: require("../img/3/9151D扁平苔藓10倍.jpg")
        },
        {
          imgSrc0: require("../img/4/10288A银屑病4倍.jpg"),
          imgSrc1: require("../img/4/10306A毛发红糠疹4倍.jpg"),
          imgSrc2: require("../img/4/8719B结节痒疹4倍.jpg"),
          imgSrc3: require("../img/4/10304A皮炎10倍.jpg")
        },
        {
          imgSrc0: require("../img/none.jpg"),
          imgSrc1: require("../img/none.jpg"),
          imgSrc2: require("../img/none.jpg"),
          imgSrc3: require("../img/none.jpg")
        }
      ]
    };
  },
  components: {
    headpage,
    footpage
  },
  created: function() {
    this.getParams();
  },
  watch: {
    // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
    $route: "getParams"
  },
  methods: {
    getParams: function() {
      // 取到路由带过来的参数
      let fileUrl = this.$route.query.fileUrl;
      this.url = fileUrl;
      let processed_file = this.$route.query.processed_file;
      this.fileName = processed_file;
      axios
        .get("http://123.206.92.104:8009/predict?processed=" + processed_file)
        .then(response => {
          let grade = response.data.grade;
          if (grade === 0) {
            this.msg = "海绵水肿型";
            let imgS = eval(this.imgList);
            this.imgSS = imgS[0];
          } else if (grade === 1) {
            this.msg = "正常";
            let imgS = eval(this.imgList);
            this.imgSS = imgS[1];
          } else if (grade === 2) {
            this.msg = "单纯型";
            let imgS = eval(this.imgList);
            this.imgSS = imgS[2];
          } else if (grade === 3) {
            this.msg = "界面皮炎型";
            let imgS = eval(this.imgList);
            this.imgSS = imgS[3];
          } else if (grade === 4) {
            this.msg = "银屑病样型";
            let imgS = eval(this.imgList);
            this.imgSS = imgS[4];
          } else if (grade === 5) {
            this.msg = "未分析";
            let imgS = eval(this.imgList);
            this.imgSS = imgS[5];
          } else {
            this.msg = "不是皮肤病理图片";
            let imgS = eval(this.imgList);
            this.imgSS = imgS[5];
          }
          if (grade !== -1) {
            $("#process").toggle();
            $("#prediction").toggle();
          } else {
            $("#process").toggle();
            this.alert = "您上传的疑似非病理图片";
            $("#window").modal("toggle");
          }
        });
    },
    ok() {
      $("#window").modal("toggle");
    },
    yes() {
      let params = {
        picID: this.fileName,
        Dr: sessionStorage.getItem("userName"),
        estimate: this.msg,
        judge: ""
      };
      axios
        .post("http://152.136.211.215:8080/user/judge", qs.stringify(params))
        .then(response => {
          console.log(response);
          if (response.data.code === 0) {
            this.alert = "反馈成功，感谢您的反馈";
          } else {
            this.alert = "反馈失败";
          }
          $("#window").modal("toggle");
        });
    },
    no() {
      $("#judge_window").modal("toggle");
    },
    sub_judge() {
      if (this.estimate == "") {
        this.alert = "请输入您的判断";
      } else if (this.judge == "") {
        this.alert = "请输入原因";
      } else {
        let params = {
          picID: this.fileName,
          Dr: sessionStorage.getItem("userName"),
          estimate: this.estimate,
          judge: this.judge
        };
        axios
          .post("http://152.136.211.215:8080/user/judge", qs.stringify(params))
          .then(response => {
            console.log(response);
            if (response.data.code === 0) {
              this.alert = "反馈成功，感谢您的反馈";
              $("#judge_window").modal("toggle");
              this.estimate = "";
              this.judge = "";
            } else {
              this.alert = "反馈失败";
            }
          });
      }
      $("#window").modal("toggle");
    }
  }
};
</script>


<style lang="less" scoped>
textarea {
  resize: none;
  margin: 0 0 0 74px;
}
@media (min-width: 300px) {
  .prediction {
    background-image: url("../img/back.png");
    position: absolute;
    height: auto;
    width: 100%;
  }
  .body {
    text-align: center;
  }
  .left {
    text-align: center;
    &__msg {
      font-size: 16px;
      color: #191970;
    }
    &__img {
      display: block;
      margin: 3% 0 0 10%;
      height: 80%;
      width: 80%;
      border: 1px solid cornflowerblue;
    }
  }
  .center {
    margin-top: 5%;
    color: #191970;
    font-size: 18px;
    &__process {
      text-align: center;
      img {
        width: 90%;
        height: 90%;
      }
    }
    &__prediction {
      display: none;
      &-msg {
        margin: 1% 0 0 0;
        background-color: #ed7d31;
        color: white;
        font-size: 25px;
      }
      &-info {
        font-size: 20px;
      }
    }
  }
  .right {
    text-align: center;
    margin-top: 5%;
    span {
      font-size: 20px;
      color: #191970;
    }
    img {
      height: 42%;
      width: 42%;
      border: 0.5px solid cornflowerblue;
    }
  }
  .footpage{
    margin-top: 5%;
  }
}
@media (min-width: 900px) {
  .prediction {
    background-image: url("../img/back.png");
    position: absolute;
    height: 100%;
    width: 100%;
  }
  .body {
    text-align: start;
    display: flex;
    flex-direction: row;
  }
  .left {
    text-align: start;
    margin: 5% 0 0 5%;
    height: 80%;
    width: 40%;
    &__msg {
      font-size: 20px;
      color: #191970;
    }
    &__img {
      display: block;
      margin: 3% 0 0 0;
      height: 85%;
      width: 85%;
      border: 1px solid cornflowerblue;
    }
  }
  .center {
    width: 20%;
    height: 10%;
    margin: 15% 0 0 4%;
    color: #191970;
    font-size: 20px;
    &__process {
      text-align: center;
      img {
        width: 90%;
        height: 90%;
      }
    }
    &__prediction {
      display: none;
      &-msg {
        margin: 10% 0 0 0;
        background-color: #ed7d31;
        color: white;
        font-size: 25px;
      }
      &-info {
        font-size: 20px;
      }
    }
  }
  .right {
    text-align: start;
    margin: 5% 0 0 10%;
    height: 80%;
    width: 40%;
    span {
      font-size: 20px;
      color: #191970;
    }
    img {
      height: 42%;
      width: 42%;
      border: 0.5px solid cornflowerblue;
    }
  }
   .footpage{
    margin-top: 7%;
  }
}
</style>




