<template>
  <div>
    <img class="logo" src="../img/logo.png" alt="logo" />
    <span class="name">Dermatopathology Analysis 皮肤病理 AI 分析</span>
    <!-- 刷新 -->
    <router-link tag="span" to="index">
      <span id="refresh" class="glyphicon glyphicon-repeat"></span>
    </router-link>
    <!-- 个人中心 -->
    <router-link tag="span" to="user">
      <span id="user" class="glyphicon glyphicon-user"></span>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";
import { mapMutations } from "vuex";
import qs from "qs";

export default {
  data() {
    return {
      username: sessionStorage.getItem("userName"),
      $filePath: "",
      filename: ""
    };
  },
  methods: {
    // 判断是否登录,如果没有登录，则提示登录，并跳回登录页面
    isLogin() {
      if (sessionStorage.getItem("userName") !== "null") {
        this.$store.commit("userStatus", sessionStorage.getItem("userName"));
      } else {
        this.$store.commit("userStatus", null);
        alert("请先登录！");
        this.$router.push({ path: "./" });
      }
      return this.$store.getters.isLogin;
    },
  },
  mounted() {
    this.isLogin();
  }
};
</script>

<style lang="less" scoped>

@media (min-width: 300px) {
    .logo {
  width: 40%;
  margin-top: 5%;
  margin-left: 30%;
}
.name{
    display: block;
    margin-top: 1%;
    margin-left: 6%;
    color: #191970;
  }
  span {
    font-size: 16px;
    margin-top: 2%;
    color: #191970;
  }
  #refresh{
    margin-top: 1%;
    margin-left: 70%;
  }
  #user {
    margin-top: 1%;
    margin-left: 5%;
  }
}
@media (min-width: 900px) {
  .logo {
    width: 19.2%;
    margin-top: 1%;
    margin-left: 5%;
  }
  .name {
    display: inline;
    margin-top: 1%;
    margin-left: 1%;
    color: #191970;
  }
  span {
    font-size: 25px;
    margin-top: 2%;
    color: #191970;
  }
  // 刷新、用户中心按钮
  #refresh{
    margin-top: 1%;
    margin-left: 15%;
  }
  #user {
    margin-top: 1%;
    margin-left: 5%;
  }
}
</style>



