<template>
  <div class="user">
    <headpage></headpage>
    <center>
      <!-- 用户信息 -->
      <div class="userInfo">
        <span>{{username}}</span>
      </div>
      <!-- 相关操作 -->
      <div class="backToIndex">
        <router-link tag="span" to="index">
          <span>返回首页</span>
        </router-link>
      </div>
      <div>
        <button
          class="btn__logout"
          type="button"
          @click="logout"
        >注销登录</button>
      </div>
    </center>
    <footpage></footpage>
    <!-- 提示框 -->
    <div
      class="modal fade"
      id="window1"
      tabindex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
            <h4 class="modal-title" id="myModalLabel">贝叶科技提示:</h4>
          </div>
          <div class="modal-body">{{msg1}}</div>
          <div class="modal-footer">
            <button type="button" id="ok" @click="ok" class="btn btn-primary">确认</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headpage from "@/components/headbar";
import footpage from "@/components/footbar";
export default {
  data() {
    return {
      username: sessionStorage.getItem("userName"),
      msg1: ""
    };
  },
  components: {
    headpage,
    footpage
  },
  methods: {
    //注销登录
    logout() {
      sessionStorage.setItem("userName", null);
      this.$store.dispatch("userName", null);
      this.msg1="注销成功"
      $("#window1").modal("toggle");
    },
    beforeRouteEnter: (to, from, next) => {
      next(vm => {
        vm.$store.dispatch("setUser", null);
      });
    },
    ok() {
      $("#window1").modal("toggle");
      this.$router.push({ path: "./" });
    }
  }
};
</script>

<style lang="less" scoped>
.user {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
.userInfo {
  margin-top: 40px;
  font-size: 20px;
  color: #191970;
}
.backToIndex {
  margin-top: 20%;
  font-size: 20px;
  color: #191970;
}
.btn__logout {
  border: none;
  background: none;
  margin-top: 5px;
  font-size: 20px;
  color: #191970;
}
</style>



